import { CButtonReport, CDatePicker, CInput, CSelect } from "@components";
import { Card, Col, Form, Row } from "antd";
import React from "react";

const CCardButtonReportAudit = ({
  forms,
  handleReset,
  handleDownloadExcel,
  handleDownloadPDF,
  handleGetData,
  onChangeStartDate,
  onChangeEndDate,
  onChangeBarcode,
}) => {
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };
  return (
    <Card style={{ backgroundColor: "aliceblue" }}>
      <Row>
        <Col span={8}>
          <Form {...formItemLayout}>
            <CDatePicker
              value={forms.start}
              label="Start Date"
              onChange={onChangeStartDate}
            />
          </Form>
        </Col>
        <Col span={8}>
          <Form {...formItemLayout}>
            <CDatePicker
              label="End Date"
              value={forms.end}
              onChange={onChangeEndDate}
            />
          </Form>
        </Col>
        <Col span={8}>
          <Form {...formItemLayout}>
            <CInput
              label="Barcode"
              value={forms.tag_number}
              id="tag_number"
              onChange={onChangeBarcode}
            />
          </Form>
        </Col>
      </Row>
      <Row style={{ float: "right" }}>
        <CButtonReport
          label="Reset"
          icon="reload"
          style={{
            marginRight: 15,
            backgroundColor: "#FFCC00",
            color: "#ffffff",
          }}
          type="primary"
          onClick={handleReset}
        />
        <CButtonReport
          label="PDF"
          icon="download"
          style={{
            marginRight: 15,
          }}
          type="primary"
          onClick={handleDownloadPDF}
        />
        <CButtonReport
          label="Excel"
          icon="download"
          style={{
            marginRight: 15,
          }}
          type="primary"
          onClick={handleDownloadExcel}
        />
        <CButtonReport
          label="Table"
          icon="download"
          type="primary"
          onClick={handleGetData}
        />
      </Row>
    </Card>
  );
};

export default CCardButtonReportAudit;
